.utility-flex-container {
  display: flex;
}
.utility-right {
  float: right !important;
}
section {
  margin-bottom: 60px;
}
.Settings #main-nav {
  padding-top: 0;
  padding-bottom: 0;
  border-right: none;
  margin-right: 5%;
  flex: 1;
  align-self: flex-start;
}
.Box {
  background: #f6f6f6;
  border: 1px solid #ddd;
  border-bottom-width: 2px;
  border-top: none;
  border-radius: 3px;
  padding: 17px;
}
.Box--bright {
  background-color: #fff;
}
.Box--Large {
  padding: 60px 40px;
}
.Settings #main-nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
ul {
  display: block;
}
.Settings #main-nav ul li.active {
  font-weight: 700;
}
.Settings #main-nav ul li {
  border-bottom: 1px solid #ddd;
  margin-left: -40px;
  margin-right: -40px;
  padding: 0;
  display: flex;
  align-items: center;
}
.Settings #main-nav ul li a {
  color: #3a3a3a;
  flex: 1;
  padding: 15px 20px 15px 25px;
  text-decoration: none;
}
.Settings #main-nav ul li i {
  padding-right: 15px;
}
.Setting__heading {
  border-bottom: 1px solid #e7e7e7;
  margin: 0 -40px 30px;
  padding-bottom: 30px;
  padding-left: 40px;
  padding-right: 40px;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  text-rendering: optimizeLegibility;
}
header .row {
  background-color: #fff;
}
